.Login{
	height: 90vh;
	display: flex;
	margin-top: 4vh;
	column-gap: 2vw;
	justify-content: center;


}
.ph{
	width: 380px;
	height: 560px;
}
.forms{
	width: 350px;
	height: 580px;
	display: flex;
	flex-direction: column;

}
.u{
	margin: 10px 0px;
	height: 360px;
	padding-bottom: 10px;
	padding-top: 10px;
	display: flex;
	flex-direction: column;
	border: 0.1px solid lightgray;
	background-color: #fafafa;;
}

.lf{
	font-family: monospace;
	font-size: 40px;
	margin: 30px 0px;
}
.logo{
	height: 80px;
}
.form{
	margin :30px 0px;
	display: flex;
	flex-direction: column;
	padding-top: 10px;
}
.fo{
	height: 45px;
	width:347px;
	background-color: #fafafa;
	text-align: center;
}
.inp{
	height:30px;
	width: 266px;
	background-color: whitesmoke;
	border-radius: 3px;
	font-size: 11px;
	color: #8e8e8e;
	border: 0.1px solid lightgray;
}

.bl{
	border: none;
	width: 268px;
	height: 37px;
	color: skyblue;
	border-radius: 10px;
	background-color: #405DE6  ;
	color: white;
	cursor: pointer;
}
.or{
	margin: 15px 0px;
	color:#8e8e8e;
}
.fb{
	color: navy;
	font-size: 14px;
	cursor: pointer;
}
.fr{
	height: 20px;
	cursor: pointer;
}
.c{
	border: 0.1px solid lightgray;
	height: 70px;
	background-color: #fafafa;

}
.signUp{
	margin: 21px 0px;
	font-size: 14px;
}
.lnks{
	display: flex;
	column-gap: 1vw;
	justify-content: center;

}
.lnk{
	height: 5vh;
	width: 120px;

}
.get{
	margin: 12px 0px;
}

@media screen and (max-width: 850px){
		.imgs{
			display: none;
		}

}

@media screen and (max-width: 450px){
	.u{
		border: none;
	}
	.c{
		border: none;
	}
}